
.rate-score {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.rate-score .star-item {
  display: inline-block;
  width: 20px;
  height: 20px;
  background-size: 100% 100%;
}
.rate-score .star-item.on {
  background: url("star_on.png");
}
.rate-score .star-item.half {
  background: url("star_half.png");
}
.rate-score .star-item.off {
  background: url("star_off.png");
}
.rate-score .star-item:last-of-type {
  margin-right: 10px;
}



