
.list-panel {
  position: fixed;
  -webkit-transition: all .3s;
  transition: all .3s;
  left: 0;
  top: 0;
  bottom: 0;
  right: 50px;
  z-index: 10;
  overflow: auto;
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
}
.list-panel.show {
  -webkit-transform: translateX(0);
          transform: translateX(0);
}
.list-panel .list {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  width: 100%;
  background-color: #fff;
  opacity: 1;
}
.list-panel .list .list-nav {
  position: fixed;
  height: 50px;
  line-height: 50px;
  text-align: center;
  top: 0;
  left: 0;
  right: 50px;
  color: #ed424b;
  background-color: #fff;
  border-bottom: 1px solid #ed424b;
}
.list-panel .list .list-nav .back {
  position: absolute;
  left: 10px;
  top: 10px;
  width: 30px;
  height: 30px;
  background: url("../../assets/images/back.png") no-repeat;
}
.list-panel .list .list-nav h3 {
  margin: 0;
}
.list-panel .list .list-content {
  background-color: #fff;
  margin-top: 50px;
}
.list-panel .list .list-content ul {
  padding: 0 15px;
}
.list-panel .list .list-content li {
  color: #333;
  height: 50px;
  line-height: 50px;
  border-bottom: 1px solid #ccc;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}



