
.container {
  background-color: #f6f7f9;
}
.nav-header header {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 45px;
  padding: 0 15px;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  font-size: 20px;
  text-indent: -8888px;
}
.nav-header header .brand {
  width: 100px;
  background: url(http://qidian.gtimg.com/qdm/img/logo-qdm.0.50.svg) no-repeat left;
  background-size: 100%;
}
.nav-header header .avatar {
  width: 24px;
  height: 24px;
  background: url(../assets/images/avatar.png) no-repeat center;
  background-size: 100%;
}
.home-nav {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding: 10px 0;
  margin: 10px 0;
  background-color: #fff;
}
.home-nav .guide-nav-div {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.home-nav .guide-nav-div > i {
  width: 24px;
  height: 24px;
  background-image: url(../assets/images/sprite.0.50.png);
}
.home-nav .guide-nav-div:nth-of-type(1) i {
  background-position: -63px -28px;
}
.home-nav .guide-nav-div:nth-of-type(2) i {
  background-position: 0 0;
}
.home-nav .guide-nav-div:nth-of-type(3) i {
  background-position: 0 -30px;
}
.home-nav .guide-nav-div:nth-of-type(4) i {
  background-position: 0 -60px;
}
.home-nav .guide-nav-div:nth-of-type(5) i {
  background-position: -30px -30px;
}
.swipe {
  height: 170px;
  padding: 0 15px;
}
.swipe img {
  width: 100%;
  height: 100%;
}



