
.top-nav {
  position: fixed;
  top: 0px;
  height: 50px;
  background: #000;
  width: 100%;
  opacity: 1;
  z-index: 9;
}
.top-nav .icon-back {
  position: absolute;
  top: 14px;
  left: 10px;
  width: 23px;
  height: 23px;
  background-image: url("data:image/png;base64,");
  background-size: contain;
}
.top-nav .icon-back a {
  display: block;
  width: 100%;
  height: 100%;
}
.top-nav .nav-title {
  position: absolute;
  top: 17px;
  left: 40px;
  color: #fff;
  font-size: 10px;
}



