
.read-container {
  font-size: 16px;
  color: #555;
  line-height: 31px;
  min-height: 600px;
  padding: 15px;
}
.read-container h4 {
  position: fixed;
  top: 0;
  left: 15px;
  right: 15px;
  height: 50px;
  line-height: 50px;
  font-size: 20px;
  color: #736357;
  /*border-bottom: solid 1px #736357;*/
  margin: 0 0 1em 0;
  letter-spacing: 2px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.read-container p {
  text-indent: 2em;
  margin: 0.5em 0;
  text-align: justify;
  letter-spacing: 0px;
  line-height: 1.5;
}
.read-container p:first-of-type {
  margin-top: 43px;
}
.read-container .btn-bar {
  z-index: 80;
  width: 80%;
  margin: 20px auto 0;
  max-width: 800px;
}
.read-container .btn-bar .btn-tab {
  padding-left: 0;
  height: 34px;
  line-height: 34px;
  background-color: #000;
  border-radius: 8px;
  border: 1px solid #858382;
  font-size: 14px;
  opacity: 0.9;
}
.read-container .btn-bar .btn-tab li {
  list-style-type: none;
  display: inline-block;
  width: 49%;
  text-align: center;
  color: #fff;
}
.read-container .btn-bar .btn-tab li:nth-child(1) {
  border-right: 1px solid #858382;
}
.read-container[bg='1'] {
  background-color: #e9dfc7;
}
.read-container[bg='1'] h4 {
  background-color: #e9dfc7;
}
.read-container[bg='2'] {
  background-color: #e7eee5;
}
.read-container[bg='2'] h4 {
  background-color: #e7eee5;
}
.read-container[bg='3'] {
  background-color: #a4a4a4;
}
.read-container[bg='3'] h4 {
  background-color: #a4a4a4;
}
.read-container[bg='4'] {
  background-color: #cdefcd;
}
.read-container[bg='4'] h4 {
  background-color: #cdefcd;
}
.read-container[bg='5'] {
  background-color: #283548;
}
.read-container[bg='5'] h4 {
  background-color: #283548;
}
.read-container[bg='6'] {
  background-color: #0f1410;
}
.read-container[bg='6'] h4 {
  background-color: #0f1410;
}
.read-container[night='true'] {
  background-color: #0f1410;
}
.read-container[night='true'] h4 {
  background-color: #0f1410;
}
.page-up {
  position: fixed;
  width: 100%;
  height: 35%;
  top: 0;
  color: rgba(0, 0, 0, 0.1);
  z-index: 5;
}
.click-mask {
  position: fixed;
  width: 100%;
  height: 30%;
  top: 35%;
  color: rgba(0, 0, 0, 0.1);
}
.page-down {
  position: fixed;
  width: 100%;
  height: 35%;
  bottom: 65px;
  color: rgba(0, 0, 0, 0.1);
  z-index: 5;
}
.top-nav-pannel-bk {
  position: fixed;
  bottom: 70px;
  height: 135px;
  background: #000;
  width: 100%;
  color: #fff;
  opacity: 0.9;
  z-index: 10003;
}
.fade-enter-active,
.fade-leave-active {
  -webkit-transition: opacity .5s;
  transition: opacity .5s;
}
.fade-enter,
.fade-leave-active {
  opacity: 0;
}



