
.bg-cover {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  opacity: 1;
  z-index: 10;
  background-color: rgba(0, 0, 0, 0.5);
  -webkit-transition: all .5s;
  transition: all .5s;
}
.bg-cover.hide {
  position: static;
  opacity: 0;
}



