
.category {
  background-color: #f6f7f9;
}
.category-header {
  height: 50px;
  background-color: #fff;
}
.category-header i {
  position: absolute;
  left: 10px;
  top: 10px;
  width: 30px;
  height: 30px;
  background: url(data:image/png;base64,);
}
.category-header h2 {
  margin: 0 40px;
  font-size: 18px;
  line-height: 50px;
  color: #ed424b;
}
.category-list {
  margin-top: 15px;
  padding: 15px;
  background-color: #fff;
}
.category-list ul li {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding-bottom: 10px;
  margin-bottom: 14px;
  border-bottom: 1px solid #ddd;
}
.category-list ul li a {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.category-list ul li .book-image {
  width: 80px;
}
.category-list ul li .book-image img {
  width: 100%;
}
.category-list ul li .book-detail {
  position: relative;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  padding: 0;
  margin-left: 20px;
}
.category-list ul li .book-detail h3 {
  font-size: 18px;
  margin-bottom: 10px;
}
.category-list ul li .book-detail p {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  line-clamp: 2;
  font-size: 14px;
  color: #969ba3;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}
.category-list ul li .book-detail .author {
  position: absolute;
  left: 0px;
  bottom: 5px;
  color: #969ba3;
  font-size: 13px;
}
.category-list ul li .book-detail .author i {
  position: relative;
  bottom: -3px;
  display: inline-block;
  width: 16px;
  height: 16px;
  background: url("../assets/images/man.png") no-repeat;
}
.category-list ul li .book-detail .category-r {
  position: absolute;
  right: 0;
  bottom: 5px;
  float: right;
  color: #969ba3;
  font-size: 10px;
}
.category-list ul li .book-detail .category-r span {
  border: 1px solid #ccc;
  border-radius: 2px;
  padding: 0 2px;
}
.category-list ul li .book-detail .category-r span:nth-child(2) {
  color: #ed424b;
}
.category-list ul li .book-detail .category-r span:nth-child(3) {
  color: #4284ed;
}



