
.recommend {
  padding: 15px 0;
  margin-bottom: 15px;
  background-color: #fff;
}
.recommend .title {
  margin-left: 15px;
  margin-bottom: 10px;
  border-left: 2px solid #ed424b;
  text-indent: 5px;
  font-size: 16px;
  line-height: 16px;
}
.recommend .list .list-ul {
  position: relative;
  overflow-x: auto;
  overflow-y: hidden;
  white-space: nowrap;
  text-indent: 7px;
}
.recommend .list .list-ul .list-li {
  display: inline-block;
  margin-right: 8px;
  width: 100px;
  /*white-space: normal;*/
}
.recommend .list .list-ul .list-li img {
  width: 100%;
  height: 125px;
}
.recommend .list .list-ul .list-li p {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}



