
.top-nav-pannel {
  position: fixed;
  bottom: 70px;
  height: 135px;
  background: none;
  width: 100%;
  color: #fff;
  font-size: 14px;
  z-index: 10004;
}
.top-nav-pannel button {
  background: none;
  border: 1px #8c8c8c solid;
  padding: 5px 40px;
  color: #fff;
  display: inline-block;
  border-radius: 16px;
}
.top-nav-pannel button:focus {
  outline: none;
}
.top-nav-pannel .child-mod {
  padding: 5px 20px;
  margin-top: 15px;
}
.top-nav-pannel .child-mod > span:first-child {
  margin-right: 20px;
}
.top-nav-pannel .bk-container {
  position: relative;
  height: 30px;
  width: 30px;
  background: #fff;
  border-radius: 15px;
  display: inline-block;
  vertical-align: -14px;
  margin-left: 10px;
}
.top-nav-pannel .bk-container .color_btn {
  height: 30px;
  width: 30px;
  border-radius: 15px;
}
.top-nav-pannel .bk-container-current {
  height: 31px;
  width: 32px;
  border-radius: 16px;
  border: 1px #ff7800 solid;
}
.top-nav-pannel .bk-container:nth-child(2) .color_btn {
  background-color: #e9dfc7;
}
.top-nav-pannel .bk-container:nth-child(3) .color_btn {
  background-color: #e7eee5;
}
.top-nav-pannel .bk-container:nth-child(4) .color_btn {
  background-color: #a4a4a4;
}
.top-nav-pannel .bk-container:nth-child(5) .color_btn {
  background-color: #cdefcd;
}
.top-nav-pannel .bk-container:nth-child(6) .color_btn {
  background-color: #283548;
}
.top-nav-pannel .bk-container:nth-child(7) .color_btn {
  background-color: #0f1410;
}



