
.bottom-nav {
  position: fixed;
  bottom: 0px;
  height: 70px;
  background: #000000;
  width: 100%;
  opacity: 1;
  z-index: 9;
  margin: 0 auto;
  text-align: center;
}
.bottom-nav .item {
  display: inline-block;
  width: 32%;
  color: #fff;
  text-align: center;
  margin: 0 auto;
}
.bottom-nav .item .item-warp {
  width: 26px;
  margin: 0 auto;
  position: relative;
}
.icon-text {
  position: absolute;
  top: 25px;
  font-size: 10px;
}
.icon-menu,
.icon-ft,
.icon-night,
.icon-day {
  position: absolute;
  top: 3px;
  left: 2px;
  width: 18px;
  height: 13px;
  background: url(data:image/png;base64,);
  background-size: contain;
}
.icon-night,
.icon-day,
.icon-ft {
  left: 1px;
}
.icon-ft {
  width: 20px;
  height: 13px;
  background: url(data:image/png;base64,);
  background-size: contain;
}
.current .icon-ft {
  top: 2px;
  left: 0px;
  width: 22px;
  height: 15px;
  background: url(data:image/png;base64,);
  background-size: contain;
}
.icon-day {
  width: 19px;
  height: 18px;
  background: url(data:image/png;base64,);
  background-size: contain;
}
.icon-night {
  left: 4px;
  width: 16px;
  height: 16px;
  background: url(data:image/png;base64,);
  background-size: contain;
}



